.HeaderComponent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 14vh; /* TitleBar 10vh + NavBar 4vh*/
  top: 0;
  z-index: 1;
  position: fixed;
}

.TitleBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: rgba(255, 255, 255, 1.0);
  padding: auto 0 auto 0;
  height: 10vh;
  background-color: rgb(8, 8, 8);
}

.TitleBar h1 {
  margin: 0;
  padding: 0;
  font-size: 1.6em;
  font-weight: 700;
}



