.NavBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 4vh;
  background-color: rgba(10, 10, 10, 0.65);
}

.NavBar nav {
  display: table;
  height: 100%;
  text-transform: uppercase;
  /* animation: review-navbar 0.4s cubic-bezier(.17,.67,.71,1.11) 0.5s both; */
}

.NavBar .NavButton {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  min-width: 100px;
  margin: 0 20px 0 20px;
  display: table-cell;
  vertical-align: middle;
  cursor: pointer;
}

.NavBar .NavButton:hover, .NavBar .NavButton.active  {
  color: black;
  background-color: white;
  -webkit-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}

.NavBar::before, .NavBar::after {
  content: '';
  position: absolute;
  width: 4vh;
  z-index: 2;
  border-bottom: 4vh solid rgba(255, 255, 255, 0.9);
}

.NavBar::before {
  left: 0px;
}

.NavBar::after {
  right: 0px;
}

@-webkit-keyframes review-navbar {
  from {
    opacity: 0.5;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  to {
    opacity: 1.0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes review-navbar {
  from {
    opacity: 0.5;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  to {
    opacity: 1.0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@media screen and (max-width: 450px) {
  .NavBar::before, .NavBar::after {
    content: '';
    border-bottom: none;
  }
}



