.Gallery {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 0;
  width: 100%;
}

.imageGrid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-height: 68vh;
  max-width: calc(3*30vmin);
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: hidden;
}

.imageGrid img {
  display: block;
  height: 28vmin;
  width: 28vmin;
  margin: 1vmin;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-filter: contrast(1.1) grayscale(100%);
          filter: contrast(1.1) grayscale(100%);
}

.imageGrid.loading img {
  -webkit-animation-play-state: paused !important;
          animation-play-state: paused !important;
}

.imageGrid.loaded img {
  -webkit-animation: 1.7s 2 ease-in-out normal 10s review-img;
          animation: 1.7s 2 ease-in-out normal 10s review-img;
}

.imageGrid.loaded a:nth-child(1) img {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}

.imageGrid.loaded a:nth-child(2) img {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}

.imageGrid.loaded a:nth-child(5) img {
  -webkit-animation-delay: 2.4s;
          animation-delay: 2.4s;
}

.imageGrid.loaded a:nth-child(6) img {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-duration: 2.3s;
          animation-duration: 2.3s;
}

@-webkit-keyframes review-img {
  0% {-webkit-filter: contrast(1.1) hue-rotate(100deg);filter: contrast(1.1) hue-rotate(100deg);}
  25% {-webkit-filter: contrast(1.1) hue-rotate(250deg);filter: contrast(1.1) hue-rotate(250deg);}
  50% {-webkit-filter: contrast(1.1) hue-rotate(300deg);filter: contrast(1.1) hue-rotate(300deg);}
  75% {-webkit-filter: contrast(1.1) hue-rotate(440deg);filter: contrast(1.1) hue-rotate(440deg);}
  100% {-webkit-filter: contrast(1.1) grayscale(100%);filter: contrast(1.1) grayscale(100%);}
}

@keyframes review-img {
  0% {-webkit-filter: contrast(1.1) hue-rotate(100deg);filter: contrast(1.1) hue-rotate(100deg);}
  25% {-webkit-filter: contrast(1.1) hue-rotate(250deg);filter: contrast(1.1) hue-rotate(250deg);}
  50% {-webkit-filter: contrast(1.1) hue-rotate(300deg);filter: contrast(1.1) hue-rotate(300deg);}
  75% {-webkit-filter: contrast(1.1) hue-rotate(440deg);filter: contrast(1.1) hue-rotate(440deg);}
  100% {-webkit-filter: contrast(1.1) grayscale(100%);filter: contrast(1.1) grayscale(100%);}
}

@media screen and (orientation: portrait) {
  .imageGrid {
    max-width: calc(2*30vmin);
  }
}

@media screen and (max-width: 450px) {
  .imageGrid {
    max-height: 100%;
    max-width: 100%;
  }

  .imageGrid img {
    height: auto;
    width: 100%;
    margin: 15px 0;
  }
}

